.trendingWrap {
    padding: 80px 40px 40px 40px;
    /* margin-top: 90px; */
  }
  
  .trendingTitle {
    color: #a41047;
  }
  
  .trendingItem {
    font-size: large;
    font-weight: 500;
    color: gray;
  }
  
  .trendingLink {
    display: block;
    padding: 10px;
    width: 100%;
    height: 100%;
    font-size: large;
    font-weight: 500;
    color: gray;
  
    &:hover {
      color: #a41047;
      box-shadow: 4px 4px 8px 2px rgba(0, 0, 0, 0.2);
    }
  }
  